<template>
  <div id="app">
    <component :is="viewName" v-if="initStatus"></component>
  </div>
</template>

<script>
import Index from './components/Index.vue';
import Setting from './components/Setting.vue';

const defaultPrizes = [
  {name: '一等奖', sort: 1, odds: 5 },
  {name: '二等奖', sort: 2, odds: 15},
  {name: '三等奖', sort: 3, odds: 30},
  {name: '四等奖', sort: 4, odds: 30},
]

export default {
  name: 'App',
  components: {
    Index,
    Setting,
  },
  data(){
    return {
      viewName: null,
      initStatus: false,
    }
  },
  mounted(){
    this.init();
  },
  methods: {
    init(){
      let storage_prizes = JSON.parse(window.localStorage.getItem('prizes'));
      if(!storage_prizes){
        window.localStorage.setItem('prizes', JSON.stringify(defaultPrizes));
      }
      let hash = window.location.hash.replace(/^#/, '');
      console.log(hash)
      if(hash==='setting'){
        this.viewName = 'Setting';
      }
      else{
        this.viewName = 'Index';
      }
      this.initStatus = true;
    }
  },
}
</script>

