<template>
  <div class="view">
    <!-- <button class="btn_setting" @click="popupSetting=true">
      <Icon name="setting" color="#FFFFFF"/>
    </button> -->
    <div class="main">
      <div class="slogan"></div>
      <div class="roulette">
        <div class="roulette_bg"></div>
        <div class="roulette_light"></div>
        <ul class="circle" 
        :class="{'transition': transAble}"
        :style="`transform: rotate(${angle}deg);`"
        @transitionend="onTransitionFinish"
        >
          <li v-for="(item,index) in prizes" :key="item.level" 
          :class="{'active': !waiting && resultIndex===index}"
          :style="`transform:rotate(${item.angle}deg);${item.clip}`"
          ><span v-html="item.name"></span></li>
        </ul>
        <button class="start" :disabled="waiting" @click="startClick"></button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Index',
  data: function(){
    return{
      angle: 0,
      waiting: false,
      transAble: false,
      resultIndex: null,
      prizes: [],
      popupSetting: true,
    }
  },
  mounted(){
    let storage_prizes = JSON.parse(window.localStorage.getItem('prizes'));
    this.prizes = this.makeLevels(storage_prizes);
  },
  methods:{
    startClick(){
      if(this.waiting) return;
      this.waiting = true;
      let random = this.roundNumber(Math.random() * 100, 2);
      let result = this.getResult(random);
      this.resultIndex = result.item.index;
      this.transAble = true;
      this.$nextTick(()=>{
        this.angle = result.angle;
      });
      
      console.log(random, result.item.name )
    },

    getResult(random){
      let min = 0;
      let arr = this.prizes;
      let deg = 360 / arr.length;
      let result = {
        item: null,
        angle: null,
      }
      for(let i in arr){
        let item = arr[i];
        if(random >= min && random < (min + item.odds)){
          result.item = JSON.parse(JSON.stringify(item));
          result.item['index'] = Number(i);
          result.angle = (360 * 20) + (i * deg) + ((random - min) / item.odds * deg) - (deg / 2);
          break;
        }
        min = min + item.odds;
      }
      return result;
    },

    onTransitionFinish(){
      this.transAble = false;
      this.waiting = false;
      this.$nextTick(()=>{
        this.angle = this.angle % 360;
      });
    },

    makeLevels(options){
      let n = options.length || 0;
      if(n===2){
        options.forEach((item,index)=>{
          item.angle = -360 * index / n;
          item.clip = '';
        });
      }
      else{
        let rad = Math.PI / n;
        let percentX = this.roundNumber(Math.tan(rad) * 50, 2) ;
        options.forEach((item,index)=>{
          item.angle = -360 * index / n;
          item.clip = `clip-path: polygon(${50-percentX}% 0%, ${50+percentX}% 0%, 50% 100%)`;
        });
      }
      
      return options;
    },

    roundNumber(num, fixed=0){
      let result = null;
      try{
        result = Number(Number(num).toFixed(fixed));
      }catch(e){
        result = NaN;
      }
      return result;
    }
  }
}
</script>

