<template>
  <div class="view">
    <NavBar title="奖项设置"
    left-text="抽奖"
    @click-left="onLeftClick"
    >
      <template #right>
        <button class="btn_add" @click="newItemClick">新增</button>
      </template>
    </NavBar>
    <CellGroup title="奖项">
      <Cell v-for="(item,index) in prizes" :key="item.level" 
      :title="item.name" 
      :value="`${item.odds}%`" 
      is-link
      @click="editItemClick(index)"
      />
    </CellGroup>

    <Popup
      class="popup_edit"
      v-model="popupEdit"
      closeable
      position="bottom"
      @close="editCloseClick"
    >
      <div class="edit_box" v-if="editItem">
        <h6>{{editItem.index>=0 ? '修改奖项' : '新增奖项'}}</h6>
        <Field
          label="名称"
          v-model="editItem.name"
          placeholder="请输入长度1~10的名称"
        />
        <Field
          label="几率"
          v-model="editItem.odds"
          placeholder="请输入0~99的数字"
        />
        <Field
          label="序号"
          v-model="editItem.sort"
          placeholder="请输排序序号"
        />
        <div class="btn_box">
          <button style="background:#aaaaaa;" @click="deletClick" v-if="editItem.index>=0">删除</button>
          <button style="background:#aaaaaa;" @click="editCloseClick" v-else>取消</button>
          <button style="background:#07c160;" @click="saveClick">保存</button>
        </div>
      </div>
      
    </Popup>
  </div>
</template>

<script>

import { NavBar, CellGroup, Cell, Popup, Field, Notify } from 'vant';
import 'vant/lib/nav-bar/style';
import 'vant/lib/cell/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/popup/style';
import 'vant/lib/field/style';
import 'vant/lib/notify/style';

export default {
  name: 'Setting',
  components: { NavBar, CellGroup, Cell, Popup, Field },
  data: function(){
    return{
      prizes: [],
      editItem: null,
      popupEdit: false,
    }
  },
  mounted(){
    let storage_prizes = JSON.parse(window.localStorage.getItem('prizes'));
    if(storage_prizes){
      // storage_prizes.forEach((item, i)=>{
      //   item['sort'] = i;
      // });
      this.prizes = storage_prizes;
    }
  },
  methods:{
    onLeftClick(){
      window.location.href = '/index.html';
    },

    newItemClick(){
      this.editItem = {
          name: '', 
          odds: '',
          sort: '', 
        };
      this.popupEdit = true;
    },

    editItemClick(index){
      let item = this.prizes[index];
      let obj = JSON.parse(JSON.stringify(item));
      obj['index'] = index;
      this.editItem = obj;
      this.popupEdit = true;
    },

    editCloseClick(){
      this.editClose();
    },

    saveClick(){
      let edit = this.editItem;
      let txt_name = edit.name.trim();
      let txt_odds = this.roundNumber(edit.odds, 2);
      let txt_sort = Number(edit.sort);
      if(!/^\S{1,10}$/.test(txt_name)){
        Notify('名称输入有误');
        return;
      }
      if(isNaN(txt_odds) || !/^[0-9\.]+$/.test(txt_odds)){
        Notify('几率输入有误');
        return;
      }
      if(!/^[1-9]+$/.test(txt_sort)){
        Notify('序号输入有误');
        return;
      }

      let temp_prizes = JSON.parse(JSON.stringify(this.prizes));
      if(edit.index != null){
        temp_prizes[edit.index] = {
          name: txt_name, 
          odds: txt_odds,
          sort: txt_sort, 
        }
      }
      else{
        temp_prizes.push({
          name: txt_name, 
          odds: txt_odds,
          sort: txt_sort,
        });
      }

      let odds_sum = temp_prizes.reduce((total,item)=>{
        return total + Number(item.odds);
      }, 0);
      odds_sum = this.roundNumber(odds_sum, 2);
      if(odds_sum > 100){
        Notify('各奖项总概率不能超过100');
        return;
      }

      temp_prizes.sort((a,b)=> a.sort - b.sort);
      this.editClose();
      this.prizes = temp_prizes;
      window.localStorage.setItem('prizes', JSON.stringify(temp_prizes));
    },

    deletClick(){
      let index = this.editItem.index;
      this.prizes.splice(index, 1);
      this.editClose();
    },

    editClose(){
      this.editItem = null;
      this.popupEdit = null;
    },

    roundNumber(num, fixed=0){
      let result = null;
      try{
        result = Number(Number(num).toFixed(fixed));
      }catch(e){
        result = NaN;
      }
      return result;
    }
  }
}
</script>

